<template>
    <div>
         <div class="login_container">
            <mt-field label="" :placeholder="unameholder" v-model="uname"></mt-field>
            <mt-field label="" type="password" :placeholder="upwdholder" v-model="upwd"></mt-field>
            <mt-field label="" type="password" :placeholder="upwdholder" v-model="yupwd"></mt-field>
            <label for="">
            
           <input type="file" @change="getFile($event)">
            </label>
            <mt-button size="large" type="primary" @click="login"></mt-button>
        </div>   
    </div>
</template>
<script>
import { Indicator } from 'mint-ui';
export default {
    data(){
        return{
               uname:"tom",
                upwd:"123",
                yupwd:"123",
                unameholder:"3-11",
                upwdholder:"3-11",
                file:"",
                data:""
        }
    },methods:{
         getFile(event) {
            this.file = event.target.files[0];
          },

         login(){
            
            var yupwd=this.yupwd
            var reg=/^\w{3,11}$/i
            var i=reg.test(this.uname);
            var u=reg.test(this.upwd);
            if(i!=true||u!=true){  
                this.$toast(""); 
                return; 
            }
            else if(this.file.length==0){
                this.$toast("")
            }
            else if(this.upwd!=yupwd){
                this.$toast("")
            }
            else{
            Indicator.open();
            let formData = new FormData();
            formData.append('file', this.file);
            let config = {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            }/* ============================================ */
            var url=`${this.DATA.login}fenxiang/upload`
            this.$http.post(url,formData,config).then(function(res){
             if(res.body.code==1){
              this.data=res.body.data
              this.insert()
              }else{
                  alert("")
              }
            })
         }},
        insert(){
                var url="login/insert";
                var obj={uname:this.uname,upwd:this.upwd,data:this.data};
                this.axios.get(url,{params:obj}).then(result=>{
                if(result.data.code==1){
                   this.$messagebox("","")
                    this.$router.push("/home/login")
                }else{
                   this.$messagebox("","")
                }
                 Indicator.close();
            })
        }
        }
    }
</script>
<style scoped>
.mint-cell-wrapper{
 background:#f7f7f7 !important;
}
.login_container{
    margin-top: 20px;
}
</style>

